import * as React from 'react';
import SalemInTheSkyMainMenu from '../assets/SalemInTheSky-mainMenu.png'
import Divider from '../components/Divider';
import GameTitle from '../components/GameTitle';
import PageContainer from '../components/PageContainer';
import { Content, ContentContainer, ContentTitle } from "../components/Content";
import { AspectRatio, Box, Center, Image } from "@chakra-ui/react";
import PlayGameButton from "../components/PlayGameButton";
import { useState } from "react";



export default function SalemInTheSkyPage() {
  const [ gameLaunched, setGameLaunched ] = useState(false);
  
  return (
    <PageContainer>
      <ContentContainer>
        <GameTitle>
          Salem in the Sky
        </GameTitle>

        <Content mb={12}>
          { gameLaunched ? (
            <AspectRatio mt={12} w="70vw" ratio={16/9}>
              <iframe src="https://itch.io/embed-upload/4097435?color=ffffff" allowfullscreen="" width="960" height="660" frameborder="0"><a href="https://urfriendxd.itch.io/saleminthesky">Play Salem in the Sky - GMTK 2021 on itch.io</a></iframe>
            </AspectRatio>
          ): (
            <Image w="960px" src={SalemInTheSkyMainMenu}/>
          )}
          { !gameLaunched ? (
            <Center>
              <PlayGameButton w="960px" onClick={() => setGameLaunched(true)} textOverride="Press to Launch Game"/>
            </Center>
          ): null}
        </Content>
  
        <Content>
          <p>
            Salem in the Sky is a quick arcade-like game where the goal is to fly through the skies as sybil whilist keeping salem the cat
            with you. Reach the highest score possible but players must beware as obstacles will come by faster and consequently become 
            more difficut.
          </p>
  
          <p>
            This game was made for the&nbsp;<a href="https://itch.io/jam/gmtk-2021">GMTK 2021 Game Jam</a> under 48 hours. The theme of the jam is <i>Joined Together</i>.
            The team consist of 6 members with talents including programmers, artists, animators, sound designers and technical artists. All the assets in the game are completely original.
          </p>
        </Content>
  
        <Divider/>
  
        <Content>
          <ContentTitle>Developing the Game Mechanic</ContentTitle>
          <p>
            During the game jam, my role was on implementing the game mechanics. Within the first 8 hours of the jam, I managed
            to get a working prototype for this rope-like mechanic. However, it was far from what we wanted. The rope still needed
            to collide with the obstacles and break when there is too much force. In comparision, getting there was much harder.
          </p>
          <p>
            I had to try multiple different implementations within the constraints of the Unity engine. I was actually becoming
            desperate towards the middle of the jam since then implementations always had a chance to produce a physics breaking
            result. However with meticilously adding tweaks and some assistance from my teammates we finally got it working.
          </p>
        </Content>
  
        <Divider/>
  
        <Content>
          <ContentTitle>Rope Physics</ContentTitle>
          <p>
            To get the basics working, I imagined the concept of pendulums and weight in physics. There is a pivot at the character at which the
            hypothetical pendulum can swing. Then the weight needs to also be heavy enough to cause a drag and hence when the player moves
            the attached weight can produce a swinging motion. Though one difference to a normal pendulum is that the rope is able to bend
            to some extent. To achieve this, the rope can be divided enough times such that there pivots with a small mass that is able to
            swing as well. A line renderer from Unity can be then used to render the rope across the pivots.
          </p>
          <p>
            Next, the obstacles need to interact with the rope. To build upon the previous step, the pivots in the rope need to be seperated
            with box colliders. Since we want the rope to only break after a certain amount of force (allowing players to recover and creating a fun moment),
            these colliders are allowed to collide with the obstacles. Only when the colliders are seperated large enough by the obstacle that
            the rope will break due to how the physics interaction allows the box colliders to seperate in the pivots.
          </p>
        </Content>
      </ContentContainer>
    </PageContainer>
  );
}