import * as React from 'react';
import { Button, Link } from "@chakra-ui/react";


export default function PlayGameButton({textOverride, w, minW, maxW, href, onClick}) {
  const text = textOverride ? textOverride : "Play the Game Here!";
  
  return (
    <>
      {href ? (
        <Link href={href} w={w} minW={minW} maxW={maxW}>
          <Button w="100%" colorScheme="teal" variant={"solid"} onClick={onClick}>
            {text}
          </Button>
        </Link>
      ) : (
        <Button w={w} minW={minW} maxW={maxW} colorScheme="teal" variant={"solid"} onClick={onClick}>
          {text}
        </Button>
      )}
    </>
  );
}