import * as React from 'react';
import { Box, Center, extendTheme, Flex, HStack, Image, VStack } from "@chakra-ui/react";
import { useState } from "react";
import { motion } from "framer-motion";
import { Link as ReactRouterLink } from 'react-router-dom'
import { Link as ChakraLink, LinkProps } from '@chakra-ui/react'


export default function GameSlide({ name, description, href, img }) {
  const [showFull, setShowFull] = useState(false);
  
  return (
    <Box
      w={"200%"} bg={ showFull ? "navColorClear" : "navColor"}
      onMouseEnter={() => setShowFull(true)} onMouseLeave={() => setShowFull(false)}
      as={motion.div} animate={{ padding: showFull ? 32 : 24 }}
    >
      <ChakraLink as={ReactRouterLink} to={href} _hover={{ textDecoration: "none" }}>
        <Center>
          <VStack>
            <Box textStyle="game-label" userSelect={"none"}>
              {name ? name : "Game Name"}
            </Box>
            <Flex
              as={motion.div} animate={{ padding: showFull ? 8 : 2 }}
            >
              <Flex
                animate={{ width: showFull ? 300 : 255 }}
                as={motion.div}>
                <Image w="100%" className="img-showcase" src={img} />
              </Flex>
              {description ? (
                <Box userSelect={"none"} p={12} as={motion.div} animate={{ scale: showFull ? 1 : 0, width: showFull ? 600 : 0, height: showFull ? 128 : 0 }}>
                  {description}
                </Box>
              ) : null}
            </Flex>
          </VStack>
        </Center>
      </ChakraLink>
    </Box>
  );
}



