import * as React from 'react';
import Divider from '../components/Divider';
import GameTitle from '../components/GameTitle';
import TheExpanseMainMenu from "../assets/TheExpanse-MainMenu.png";
import TheExpanseLongOrbitVid from "../assets/TheExpanse-longOrbit.webm";
import TheExpanseShortOrbitVid from "../assets/TheExpanse-shortOrbit.webm";
import TheExpanseManipulateVid from "../assets/TheExpanse-manipulate.webm";
import PageContainer from '../components/PageContainer';
import PageNavbar from '../components/PageNavbar';
import { AspectRatio, Center, Flex, Image } from "@chakra-ui/react";
import PlayGameButton from "../components/PlayGameButton";
import { Content, ContentContainer, ContentTitle } from "../components/Content";
import { useState } from "react";


export default function TheExpansePage() {
  const [ gameLaunched, setGameLaunched ] = useState(false);
  
  return (
    <PageContainer>
      <ContentContainer>
        <GameTitle>
          The Expanse
        </GameTitle>
  
        <Content mb={12}>
          { gameLaunched ? (
            <AspectRatio mt={12} w="70vw" ratio={16/9}>
              <iframe src="https://itch.io/embed-upload/2430815?color=ddeedd" allowfullscreen="" width="980" height="628" frameborder="0"><a href="https://gastricsparrow.itch.io/the-expanse">Play The Expanse on itch.io</a></iframe>
            </AspectRatio>
          ): (
            <Image w="960px" src={TheExpanseMainMenu}/>
          )}
          { !gameLaunched ? (
            <Center>
              <PlayGameButton w="960px" onClick={() => setGameLaunched(true)} textOverride="Press to Launch Game"/>
            </Center>
          ): null}
        </Content>
  
        <Content>
          <p>
            The expanse is a puzzle space simulation game. Players must navigate their spacecraft across the trechorous void of
            space. Space travel is much different than other modes of transport, they must rely on orbital mechanics to get there.
            They can manipulate this by having the power to control the strength of gravity, allowing them to boost the momentum
            of the spacecraft by preforing gravity slingshots (commonly known as gravity assists). However, going through
            space without spectacularly crashing the spacecraft may be difficult.
          </p>
  
          <p>
            This is one of the first few games that I have created. The game was made by a team 3 and took about a month to develop.
            It developed under Introduction to Game Design subject at the University of Technology Sydney (UTS).
          </p>
        </Content>
  
        <Divider/>
  
        <Content>
          <ContentTitle>Orbital Mechanics</ContentTitle>
  
          <Flex justifyContent="center">
            <video width="700" preload autoPlay muted style={{ pointerEvents: "none" }} disablePictureInPicture loop>
              <source src={TheExpanseLongOrbitVid} type="video/webm" />
            </video>
          </Flex>
  
          <p>
            The game is based on newtonian physics which is efficient and accurate enough for this case. Since newtonian physics tend to be
            less accurate with extremely high and low sources of gravity, we need a couple of tweaks that prevent biazarre behaviour in those circumstances.
          </p>

          <Flex justifyContent="center">
            <video width="700" preload autoPlay muted style={{ pointerEvents: "none" }} disablePictureInPicture loop>
              <source src={TheExpanseShortOrbitVid} type="video/webm" />
            </video>
          </Flex>
  
          <p>
            I also worked a lot on the
            orbital line prediction which took a good chunk amount of time to implement. In essence, the game has to run multiple steps
            of the simulation into the future to be able to predict an accurate trajectory for the spacecraft. Since players are able
            to alter the the trajectory by manipulating gravity, the entire prediction has to be done in regular intervals.
          </p>
        </Content>
  
        <Divider/>
  
        <Content>
          <ContentTitle>Game Mechanics</ContentTitle>
          <p>
            I think one of the most interesting thing that makes this game unique are the game mechanics. One of the greatest challenges
            that we have is by delving into innovation, it created a risk such that a larger amount of time is required to get it right.
            There are very minimal reference points and hence we had to slowly find the right feel for the mechanic.
          </p>

          <Flex justifyContent="center">
            <video width="700" preload autoPlay muted style={{ pointerEvents: "none" }} disablePictureInPicture loop>
              <source src={TheExpanseManipulateVid} type="video/webm" />
            </video>
          </Flex>
  
          <p>
            I spent a lot of time perfecting the main game mechanic of controling gravity and orbitals. These need to support the core
            idea of players doing gravitational slingshots to maneuver. Most importantly, the mechanic had to be easy and intuitive for
            players. It was discovered early in the project that the mechanic was too difficut and needed more tweaking but when done
            just right, mastery can be both fun and challenging. Despite me spending the majority of the project on this,
            I think it is still far from perfect.
          </p>
          <p>
            One notion to understand as a game designer is that it has to feel right for the players, which doesn't necessarily mean
            that is has to be accurate or realistic. Making a platformer controls feel nice means accommodating to the fact that
            our human perceptions are flawed. At one point, I realised that it isn't much fun to be stuck in space because you have
            very low momentum. To combat this, I made gravity controls stronger for low gravity situations which made the whole
            control scheme feel much more intuitive. This is a quirk that players will not usually notice akin to coyote time in platformers.
            Conversely I made the gravity controls weaker in high gravity (close to a body) to prevent sudden and uncontrollable acceleration.
          </p>
        </Content>
      </ContentContainer>
    </PageContainer>
  );
}