import * as React from "react";
import { useRef } from "react";
import { useEffect } from "react";



export default function PageScroller({ sections, startPage, children }) {
  let containerRef = useRef(null)
  let sectionRefs = sections;
  let lastScrollY = window.scrollY;

  function findDblIndex(arr, target) {
    let bestLeft = -1;
    let bestLeftIndex = -1;
    let bestRight = Number.MAX_VALUE;
    let bestRightIndex = -1;
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] <= target && arr[i] > bestLeft) {
        bestLeft = arr[i];
        bestLeftIndex = i;
      }
      if (arr[i] >= target && arr[i] < bestRight) {
        bestRight = arr[i];
        bestRightIndex = i;
      }
    }
    return [ bestLeftIndex, bestRightIndex ];
  }
  
  function getScrollSections() {
    const scrollSections = [];
    for (let child of containerRef.current.children) {
      scrollSections.push(child.getBoundingClientRect().y + window.scrollY)
    }
    scrollSections[0] = 0; // Force the first section to always be the top most section
    return scrollSections;
  }

  function handleScrollEvent(event) {
    const currentScrollY = window.scrollY;
    const scrollSections = getScrollSections();
    const [bestLeftIndex, bestRightIndex] = findDblIndex(scrollSections, currentScrollY);
    // console.log([bestLeftIndex, bestRightIndex], " vs ", scrollSections);

    if (window.scrollY > lastScrollY) {
      if (bestRightIndex != -1) {
        // console.log("SCROLL DOWN TO ", scrollSections[bestLeftIndex + 1]);
        window.scrollTo({ top: scrollSections[bestLeftIndex + 1], behavior: "smooth" });
      }
    } else if (window.scrollY < lastScrollY) {
      if (bestLeftIndex != bestRightIndex && bestRightIndex != -1) {
        // console.log("SCROLL UP TO ", scrollSections[bestLeftIndex]);
        window.scrollTo({ top: scrollSections[bestLeftIndex], behavior: "smooth" });
      }
    }
    lastScrollY = window.scrollY
  }

  useEffect(() => {
    // Set up initial scroll position
    const scrollSections = getScrollSections();
    const pageToSet = startPage ? startPage : 0
    window.scrollTo({ top: scrollSections[pageToSet], behavior: "instant" });
    
    // Register scroll events
    window.addEventListener("scroll", handleScrollEvent);
    return () => {
      window.removeEventListener("scroll", handleScrollEvent);
    }
  })

  return <div ref={containerRef}>
    {children}
  </div>
}