import { MotionBox, MotionSpan } from "./MotionBox";
import { animate, motion, useMotionValue, useTransform } from "framer-motion";
import { useEffect, useState } from "react";
import { Box, Flex, Wrap } from "@chakra-ui/react";



export default function TextTypewriter(props) {
  const textArray = props.text && Array.isArray(props.text) ? props.text : [props.text];
  const shouldStay = props.shouldStay || false;
  
  const [textIndex, setTextIndex] = useState(0);
  const textLength = useMotionValue(0);
  let renderedText = useTransform(textLength, latest => textArray[textIndex].substring(0, latest));

  useEffect(() => {
    let textAnim;
    const textToDurationScale = 0.05;
    const targetTextLength = textArray[textIndex].length;
    
    if (textIndex === textArray.length - 1 && shouldStay) {
      textAnim = animate([
        [textLength, targetTextLength, { duration: targetTextLength * textToDurationScale, delay: 1 }],
      ]);
    } else {
      textAnim = animate([
        [textLength, targetTextLength, { duration: targetTextLength * textToDurationScale, delay: 1 }],
        [textLength, 0, { duration: 0.6, delay: 2 }]
      ]);
    }
    
    textAnim.then(() => {
      if (textIndex + 1 < textArray.length)
        setTextIndex(textIndex + 1);
    });
    
    return () => textAnim.stop();
  }, [textIndex]);
  
  return (
    <Box as={"paragraph"} {...props}>
      <MotionSpan>
        {renderedText}
      </MotionSpan>
      <MotionBox
        mx={1}
        bg={"black"} w={"0.4em"} h={"1.2em"}
        display={"inline-block"}
        transform={"auto"} 
        translateY={2}
        animate={{opacity: [0, 1, 1, 0]}}
        transition={{
          duration: 0.9,
          repeat: Infinity,
          repeatType: "loop",
        }}
      />
    </Box>
  )
}