import { Box, Center, Flex, VStack } from "@chakra-ui/react";
import * as React from "react";
import { MotionBox } from "./MotionBox";
import { isVisible } from "@testing-library/user-event/dist/utils";


export function ContentTitle(props) {
  return (
    <Box className="title-segment" mb={2} p={8}>
      {props.children}
    </Box>
  );
}

export function Content({children, ...props }) {
  return (
    <MotionBox 
      initial={{ opacity: 0, translateY: 120 }}
      whileInView={{ opacity: 1, translateY: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.55, ease: "easeIn" }}
      {...props}>
      <Center flexDir="column">
        {children && children.map((child, i) => child && child.type.name === "ContentTitle" ? child : null)}
        <Flex flexDir="column" w="70vw" className="content-segment" data-aos="fade-up">
          {children.map((child, i) => child && child.type.name !== "ContentTitle" ? child : null)}
        </Flex>
      </Center>
    </MotionBox>
  );
}

export function ContentContainer(props) {
  return (
    <Flex justifyContent="center">
      <VStack>
        {props.children}
      </VStack>
    </Flex>
  );
}